<template lang="html">
  <div class="convert-point-input">
    <span>{{text}}</span>
    <input v-if="inpuType === 'text'" v-model="curValue" @input="$emit('input', curValue)" type="text" name="" value="" :placeholder="placeholder">
    <input v-if="inpuType === 'password'" v-model="curValue" @input="$emit('input', curValue)" type="password" name="" value="" :placeholder="placeholder">
  </div>
</template>

<script>
export default {
  props: {
    value: {},
    text: {
      type: String,
      default: '充值金额'
    },
    placeholder: {
      type: String,
      default: '请输入'
    },
    inpuType: {
      type: String,
      default: 'text'
    }
  },
  watch: {
    value (val) {
      this.curValue = val
    }
  },
  data () {
    return {
      curValue: this.value
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';

.convert-point-input {
  padding: .11rem .22rem;
  font-size: .3rem;
  line-height: .3rem;
  background: @whiteColor;
  span {
    width: 1.2rem;
  }
  input {
    width: 4.8rem;
    height: .68rem;
    border-radius: .05rem;
    padding-left: .2rem;
    margin-left: .46rem;
    color: @lightColor;
    border: .01rem solid @lightColor;
    &::placeholder {
      color: @lightColor;
    }
  }
}
</style>



