<template lang="html">
  <div class="convert-point-page">

    <!-- 头部 -->
    <default-head header="充值">  </default-head>

    <form action="#">
      <!-- 列表 -->
      <convert-point-ratio></convert-point-ratio>
      <convert-point-input text="充值金额" v-model="cashInfo.disamount" data-vv-name="充值金额" v-validate="'required'" name="disamount"
      placeholder="请输入充值召币金额"></convert-point-input>
      <convert-point-input inpuType="password" text="交易密码" v-model="cashInfo.payPwd" data-vv-name="交易密码" v-validate="'required'" name="disamount"
      placeholder="请输入您的支付密码"></convert-point-input>

      <!-- 保存按钮 -->
      <foot-btn text="立即充值" @click.native="payCashRightNow"></foot-btn>
    </form>
  </div>
</template>

<script>
import api from '@/api'
import utils from '@/utils/utils'
import minutils from '@/utils/minUtils'
import defaultHead from '@/components/head/defaultHead'
import convertPointRatio from '@/components/convertPoint/convertPointRatio'
import convertPointInput from '@/components/convertPoint/convertPointInput'
import footBtn from '@/components/common/footBtn'

export default {
  components: { defaultHead, convertPointRatio, convertPointInput, footBtn },
  data () {
    return {
      cashInfo: {
        disamount: this.$route.query.disamount || '',
        payPwd: ''
      }
    }
  },
  computed: {
    userId () {
      return utils.userId()
    }
  },
  created () {
    this.initPage()
  },
  methods: {
    initPage () {
      this.ajaxVerifyPayPassword({userId: this.userId})
    },
    payCashRightNow () {
      this.$validator.validateAll().then(() => {
        this.ajaxVerifyPayPasswordIsTrue({
          userId: this.userId,
          payPassword: this.cashInfo.payPwd
        }, () => {
          this.$router.push({
            path: 'payconfirm',
            query: {payPwd: this.cashInfo.payPwd, disamount: this.cashInfo.disamount, cash: this.$route.query.cash || '', type: this.$route.query.type, disamountId: this.$route.query.disamountId || ''}
          })
        })
      }).catch((e) => {
        minutils.Toast({ message: this.errors.all()[0], position: 'top' })
      })
    },
    hasPayPwd (path) {
      let self = this
      minutils.MessageBoxConfirm({
        message: '您暂未设置支付密码, 请设置支付密码后充值',
        options: {confirmButtonText: '立即设置', cancelButtonText: '取消设置'},
        success (action) {
          self.$router.push(path)
        },
        fail (action) {
          self.$router.go(-1)
        }
      })
    },
    ajaxVerifyPayPassword (data) {
      api.verifyPayPassword(data).then(response => {
        let result = response.body
        if (result.isSuccess) {
          !result.data && this.hasPayPwd('/modifypayword')
        }
      })
    },
    ajaxVerifyPayPasswordIsTrue (data, callback) {
      api.verifyPayPasswordIsTrue(data).then(response => {
        let result = response.body
        if (result.isSuccess) {
          if (result.data) {
            callback()
          } else {
            minutils.Toast({ message: '支付密码错误, 请重新输入', position: 'top' })
          }
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
@import '../assets/css/variable.less';

.convert-point-page {
  padding-top: .88rem;
  form {
    padding-bottom: .2rem;
    background: @whiteColor;
  }
}
</style>



